/* General button styling */

.zoom-control-container {
    position: fixed;
    bottom: 10px;
    right: 10px;
    background-color: rgba(255, 255, 255, 0.7); /* semi-transparent white */
    padding: 8px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.zoom-control-btn {
    border: none;
    background-color: #f3f3f3;
    color: #333;
    padding: 6px 12px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    transition-duration: 0.4s;
    cursor: pointer;
    border-radius: 50%; /* Making the buttons circular */
    width: 36px;
    height: 36px;
    line-height: 24px; /* Centering the '+' '-' and 'fit' text vertically */
}

/* Hover state styling */
.zoom-control-btn:hover {
    background-color: #ddd; /* A lighter shade on hover */
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2); /* Subtle shadow on hover */
}

/* Specific button stylings */
.zoom-in-btn {
    background-color: #00A600; /* Green for zoom in */
}

.zoom-out-btn {
    background-color: #E81123; /* Red for zoom out */
}

.zoom-fit-btn {
    background-color: #F2C811; /* Yellow for fit */
    align-content: center;
}

